// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000000;
$color-white: #ffffff;
$color-primary: #ff6464;
$color-secondary: #00a8cc;
$color-dark: #21243d;
$color-AquaSpring: #edf7fa;
$color-BlueZodiac: #142850;
$color-RegentGray: #8695a4;

// FONTS

$font-family-roboto: 'Roboto', sans-serif;

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); //  
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;
